@use '@mview/scss/utilities/functions' as *;
@use 'sass:color';

.form-group {
  width: fit-content;

  .select-wrapper {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    position: relative;

    .select {
      user-select: none;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      cursor: pointer;
      background-color: white;
      border: 1px solid pick-color('primary', 'fog');
      font-size: 13px;
      color: pick-color('primary', 'charcoal');
    }

    .select-arrow {
      cursor: pointer;
      position: absolute;
      right: 4px;
      top: 50%;
      transform: translateY(-50%);
      color: pick-color('primary', 'charcoal');

      div {
        transition: transform 0.25s;

        &.expanded {
          transform: rotate(-180deg);
        }
      }
    }
  }

  &.disabled {
    &:focus {
      outline: none;
    }

    .select-wrapper {
      .select {
        cursor: not-allowed;
        color: pick-color('primary', 'fog');
        border: 1px solid pick-color('secondary', 'steam');
        background-color: pick-color('secondary', 'steam');

        &::placeholder {
          color: pick-color('primary', 'fog');
        }
      }

      .select-arrow {
        cursor: not-allowed;
        color: pick-color('primary', 'fog');
      }
    }
  }
}
